* {
    box-sizing: border-box;
    margin: 0;
}

.body {
    font-family: 'Montserrat', 'Times New Roman', sans-serif;
    font-style: normal;
    font-weight: 400;
    min-width: 375px;
}

.container {
    max-width: 1380px;
    min-width: 375px;
    margin: 0 auto;
    padding: 0 30px;
}

.img-responsive {
    max-width: 100%;
    height: auto;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.a {
    &:hover {  
        color: $active-hover;
    }
}

.picture {
    background-image: url(../img/team.png);
}

.backgroung-main {
    background-color: $grey-bg;
}